Mock.mock(/api\/getMenuPageBoxProxy(\?.*)/, 'get', (options) => {
    const boxItem = {
        href: "#",
        name: "小米智能电视",
        src: "https://cdn.cnbj1.fds.api.mi-img.com/mi-mall/c2e3fdbf6fe7a13c06644f16c8dc8877.png?thumb=1&w=40&h=40&f=webp&q=90"
    };
    return Res(new Array(getRandom(1, 24)).fill(boxItem));
});

function getRandom(min, max) {
    return Math.floor(Math.random() * (max - min + 1)) + min;
}

